// 後台訂單頁面
<template>
  <div>
  <Loading :active="isLoading"></Loading>
    <div class="text-end">
    </div>
    <table class="table mt-4">
        <thead>
            <tr>
            <th width="120">購買時間</th>
            <th width="120">Email</th>
            <th width="120">購買品項</th>
            <th width="120">應付金額</th>
            <th width="120">是否付款</th>
            <th width="200">編輯</th>
            </tr>
        </thead>
        <tbody>
            <template v-for="order in orders" :key="order.id">
            <tr>
              <td>{{$filters.date(order.create_at)}}</td>
              <td>{{order.user.email}}</td>
              <td >
                <ul class="ps-0">
                  <li class="list-unstyled" v-for="product in order.products" :key="product.id">
                    {{product.product.title}}數量:{{product.qty}}/{{product.product.unit}}
                  </li>
                </ul>
              </td>
              <td class="text-right">
                  {{Math.round(order.total)}}
              </td>
              <td>
                <div class="form-check form-switch">
                  <input class="form-check-input" type="checkbox" :id="`paidSwitch${order.id}`"
                        v-model="order.is_paid"
                        @change="updatePaid(order)">
                  <label class="form-check-label" :for="`paidSwitch${order.id}`">
                    <span v-if="order.is_paid">已付款</span>
                    <span v-else>未付款</span>
                  </label>
                </div>
              </td>
              <td>
                  <div class="btn-group">
                  <button class="btn btn-outline-primary btn-sm" @click="openModal(false, order)">詳細檢視</button>
                  <button class="btn btn-outline-danger btn-sm" @click="openDelOrderModal(order)">刪除</button>
                  </div>
              </td>
            </tr>
            </template>
        </tbody>
    </table>
      <OrderModal :order="tempOrder" ref="orderModal" @update-order="closeModal"></OrderModal>
      <DelModal :item="tempOrder" ref="delModal" @delete-something="delOrder"></DelModal>
  </div>
</template>

<script>
import OrderModal from '@/components/orderModal.vue'
import DelModal from '@/components/DeleteModal.vue'
export default {
  data () {
    return {
      orders: {}, // 存取api回傳的訂單
      isNew: false,
      isLoading: false,
      tempOrder: {}
    }
  },
  components: {
    OrderModal,
    DelModal
  },
  methods: {
    getOrder (page = 1) {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/orders?page=${page}`
      this.isLoading = true
      this.$http.get(url)
        .then((res) => {
          if (res.data.success) {
            this.orders = res.data.orders
            this.isLoading = false
            // this.getProduct()
          }
        })
    },
    updatePaid (order) {
      this.isLoading = true
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/order/${order.id}`
      const paid = {
        is_paid: order.is_paid
      }
      this.$http.put(api, { data: paid })
        .then((res) => {
          this.isLoading = false
          const orderComponent = this.$refs.orderModal
          orderComponent.hideModal()
          this.getOrder()
          this.$pushMessageState(res, '更新付款狀態')
        })
    },
    openModal (isNew, order) {
      this.tempOrder = { ...order }
      this.isNew = false
      const orderComponent = this.$refs.orderModal
      orderComponent.showModal()
    },
    closeModal () {
      const orderComponent = this.$refs.orderModal
      orderComponent.hideModal()
    },
    openDelOrderModal (item) {
      this.tempOrder = { ...item }
      const delComponent = this.$refs.delModal
      delComponent.showDeleteModal()
    },
    delOrder () {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/order/${this.tempOrder.id}`
      this.isLoading = true
      this.$http.delete(url).then((res) => {
        this.$pushMessageState(res, '訂單刪除')
        if (res.data.success) {
          const delComponent = this.$refs.delModal
          delComponent.hideDeleteModal()
          this.getOrder()
        }
      })
    }
  },
  created () {
    this.getOrder()
  }
}
</script>
